.is-hidden {
  display: none;
}

body {
  background-color: #def3f3;
}

.cat-info {
  text-align: center;
  background-color: #d8f3fb;
  border: 7px ridge #77cff5;
  border-radius: 15px;
  margin: 5px;
  padding: 40px;
}

.loader {
  width: 95px;
  height: 95px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  margin: 50px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 120px;
  height: 120px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

